      pressed: {
        red: false,
        blue: false,
        yellow: false,
        green: false
      },
      delayBetweenRounds: 500,
      delayBetweenFlashes: 200,
      flashDuration: 500
    };
  },
  computed: {
    playable() {
      return this.pressable;
    }
  },
  methods: {
    disablePlayer() {
      this.pressable = false;
    },
    enablePlayer() {
      this.pressable = true;
    },
    press(color) {
      if (!this.playable) {
        return;
      }

      this.disablePlayer();
      this.player.push(color);
      this.flashSingle(color).then(() => {
        this.checkLastPress();
      });
    },
    checkLastPress() {
      let idx = this.player.length - 1;
      if (this.player[idx] === this.simon[idx]) {
        if (this.player.length === this.simon.length) {
          this.winRound();
        } else {
          // let the player continue with his sequence
          this.enablePlayer();
        }
      } else {
        this.loseGame();
      }
    },
    winRound() {
      if (this.round === this.maxRounds) {
        this.winGame();
      } else {
        setTimeout(() => {
          this.startNextRound();
        }, this.delayBetweenRounds);
      }
    },
    winGame() {
      this.round = "YOU WON!";
      this.playSound(sounds.win);
    },
    loseGame() {
      this.round = "YOU LOST!";
    },
    startNextRound() {
      this.disablePlayer();
      this.player = [];
      this.round++;
      this.simon.push(this.getRandomColor());
      this.simonSay(this.simon).then(() => {
        this.enablePlayer();
      });
    },
    resetGame() {
      this.disablePlayer();
      this.round = 0;
      this.player = [];
      this.simon = [];
    },
    startGame() {
      this.resetGame();
      this.startNextRound();
    },
    getRandomColor() {
      let colors = ["red", "blue", "yellow", "green"];
      let rnd = Math.floor(Math.random() * colors.length);
      return colors[rnd];
    },
    simonSay(sequence) {
      return this.flashSequence(sequence, this.delayBetweenFlashes);
    },
    flashSequence(sequence, gapTime) {
      gapTime;
      return new Promise(resolve => {
        if (sequence && sequence.length > 0) {
          let color = sequence[0];
          this.flashSingle(color).then(() => {
            setTimeout(() => {
              this.simonSay(sequence.slice(1)).then(() => resolve());
            }, gapTime);
          });
        } else {
          resolve();
        }
      });
    },
    flashSingle(color) {
      return new Promise(resolve => {
        this.pressed[color] = true;
        this.playSound(sounds[color]);
        setTimeout(() => {
          this.pressed[color] = false;
          resolve();
        }, this.flashDuration);
      });
    },
    playSound(sound) {
      if (sound) {
        var audio = new Audio(sound);
        audio.play();
      }
    }
  }
};
</script>

